<template>
   <div>
      <div class="container">
        <div class="page-title-normal">
          <h2 class="page-title-h2"><span></span></h2>
        </div>
        <!--  -->
        <div class="check-step">
          <ul>
            <li class="cur"><span></span> </li>
            <li class="cur"><span></span> </li>
            <li class="cur"><span></span> </li>
            <li class="cur"><span></span> </li>
          </ul>
        </div>

        <div class="order-create">
          <div class="order-create-pic"><img src="/static/ok-2.png" alt=""></div>
          <div class="order-create-main">
            <h3>! <br>!</h3>
            <p>
              <span>{{orderId}}</span>
              <span>{{orderTotal|currency('')}}</span>
            </p>
            <div class="order-create-btn-wrap">
              <div class="btn-l-wrap">
                <router-link class="btn btn--m" to="/cart"></router-link>
              </div>
              <div class="btn-r-wrap">
                <router-link class="btn btn--m" to="/"></router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>
<script>
import Public from '../Public'
export default {
  mixins:[Public],
  data(){
      return {
          orderId:'',
          orderTotal:0
      }
  },
  mounted(){
      let orderId = this.$route.query.orderId  
      if(!orderId) { return } 
      this.$http.get('/users/orderDetail?orderId=' + orderId).then(res =>{
        res = res.data
        if(res.status === '0'){
            this.orderId = orderId
            this.orderTotal = res.result.orderTotal
        }
      })
  }
}
</script>

<style>

</style>




